<i18n>
    {
    "zh": {
    "sign": {
    "accountNameNotMatch": "帐号密码不一致",
    "login": "登陆",
    "name": "名称",
    "password": "密码",
    "notEmpty": "不能为空"
    }

    },
    "en": {
    "sign" : {
    "accountNameNotMatch": "Account is not match with password",
    "login": "Login",
    "name": "Name",
    "password": "Password",
    "notEmpty": "Cannot be empty"
    }
    }
    }
</i18n>

<template>
    <div class="sign sign-layout">
        <el-form :model="info" :rules="rules" ref="form" class="form-container">
            <h2>{{$t('sign.login')}}</h2>
            <el-form-item :label="$t('sign.name')" prop="username">
                <el-input v-model="info.username"></el-input>
            </el-form-item>
            <el-form-item :label="$t('sign.password')" prop="password">
                <el-input v-model="info.password" type="password" @keyup.enter.native="login"></el-input>
            </el-form-item>
            <div class="footer">
                <el-button type="primary" @click="login">{{$t('sign.login')}}</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
  import cookie from 'js-cookie/src/js.cookie'
  import service from './service'

  export default {
    name: 'sign',
    data () {
      return {
        info: {
          username: '',
          password: ''
        },
        rules: {
          username: [
            {
              require: true,
              trigger: 'blur',
              message: this.$t('sign.notEmpty')
            }
          ],
          password: [
            {
              require: true,
              trigger: 'blur',
              message: this.$t('sign.notEmpty')
            }
          ]
        }
      }
    },
    methods: {
      login () {
        this.$refs['form'].validate(valid => {
          if (valid) {
            var params = this.info
            service.login(params).then(res => {
              console.log('res', res)
              if (res.data.status === '1') {
                cookie.set('token', res.data.token)
                this.$router.push({path: 'index/home'})
              } else {
                this.$message({type: 'error', message: this.$t('sign.accountNameNotMatch')})
              }
            })
          }
        })
      }
    },
    components: {}
  }
</script>

<style scoped>
    .sign.sign-layout {
        width: 100%;
        height: 100%;
        display: flex;
        background-image: url('../../assets/img/bg.jpeg');
        background-size: 100% 100%;
        background-repeat: no-repeat;
    }

    h2 {
        font-weight: bold;
    }

    .form-container {
        margin: auto;
        width: 300px;
        background-color: white;
        padding: 30px;
        border-radius: 4px;
        /*margin-top: 50%;*/
        /*transform: translateY(-50%);*/
    }

    .footer {
        margin-top: 40px;
    }

    .footer .el-button {
        width: 100%;
    }
</style>


